import React, { Component } from 'react';
import Layout from './Layout/Layout';
//import FeedbackChoice from './FeedbackChoice/FeedbackChoice';
//import FeedbackStat from './FeedbackStat/FeedbackStat';
import shortid from 'shortid'


export default class App extends Component {
  state = {
  good: 0,
  neutral: 0,
  bad: 0
  }

  onLeavefeedback = ( option ) => {
    console.log(option);
    this.setState(
      { [option] : this.state[option] + 1}
    );
  }
  render() {
    const options = ['good', 'neutral', 'bad'];
    
    return (
      <>
        <Layout title="Pleas leave feedback">
          {options.map((option) => (
            <button type='button' className='button' key={shortid.generate()} onClick={this.onLeaveFeedback}>
            {option}
                </button>))}
        </Layout>
        <Layout title="Statistics">
            <div>{this.state}</div>
        </Layout>
      </>
    )
  }
}

